@import "../../common/styles/slider-template-triple";
@import "../../common/styles/popup-button";
@import "../../common/styles/components-notice";

.elementor-widget-coca_bais_before_after_image_slider {
	display: block;
	@include slider-template;
	@include popup-button;

	.coca-bais-triple-container {
		display: block;

		&:not(.coca-image-compare-triple-container) {
			display: none;
		}

		img {
			border: none;
			border-radius: 0;
			box-shadow: none;
		}
	}

	.components-notice {
		@include components-notice;
	}
}

body.elementor-editor-active .coca-bais-shortcode .coca-bais-triple-container {
	display: block !important;
	opacity: 1 !important;

	.image-wrapper:not(:first-child) img {
		display: none;
	}
}
