// Class Prefix
$pluginPrefix: "coca-image-compare" !default;


@mixin slider-template() {

	.left-arrow,
	.right-arrow {
		transition: 300ms ease-in-out;
	}

	.template-style01 {

		.scroller {
			--coca-image-compare-handle-radius: 0;
			margin-left: calc(-1 * var(--coca-image-compare-handle-stroke) + 3px);
			transform: rotate(45deg);

			&::before,
			&::after {
				display: none;
			}

			.scroller__thumb {
				transform: rotate(138deg);
			}
		}

	}

	.template-style02,
	.template-style03 {

		.after-label,
		.before-label {
			top: 80%;
			padding: 16px 30px;
			margin-top: 0;
			line-height: 1.5;
		}
	}

	.template-style02,
	.template-style03,
	.template-style04 {

		.before-label {
			left: 0;
			border-top-right-radius: 50px;
			border-bottom-right-radius: 50px;
		}

		.after-label {
			right: 0;
			border-top-left-radius: 50px;
			border-bottom-left-radius: 50px;
		}

	}

	.template-style02,
	.template-style05 {

		.scroller .scroller__thumb {

			.left-arrow {
				border-right-color: #737373;
			}

			.right-arrow {
				border-left-color: #737373;
			}
		}

	}

	.template-style02 {

		.scroller {
			--coca-image-compare-handle-circle-width: 30px;
			--coca-image-compare-handle-circle-height: 126px;
			--coca-image-compare-handle-stroke: 1px;
			margin-left: calc(-1 * var(--coca-image-compare-handle-stroke) + 9px);
			background: #fff;

			.scroller__thumb {

				.left-arrow {
					left: 0;
				}

				.right-arrow {
					right: 0;
				}
			}
		}
	}

	.template-style03 {

		.scroller {
			--coca-image-compare-handle-circle-width: 85px;
			--coca-image-compare-handle-circle-height: 85px;
			margin-left: calc(-1 * (var(--coca-image-compare-handle-stroke) + 18px));

			.scroller__thumb .left-arrow {
				margin-left: calc(var(--coca-image-compare-handle-circle-width) / 2 - 19px);
			}

			.scroller__thumb .right-arrow {
				margin-right: calc(var(--coca-image-compare-handle-circle-width) / 2 - 19px);
			}
		}
	}

	.template-style04 {

		.scroller {
			border: none;
			box-shadow: none;

			&::before {
				top: 0;
				margin-bottom: 0;
				margin-left: 0 !important;
			}

			&::after {
				bottom: 0;
				margin-top: 0;
				margin-left: 0 !important;
			}
		}

		.after-label,
		.before-label {
			top: 10%;
			padding: 16px 30px;
			margin-top: 0;
			line-height: 1.5;
		}
	}

	.template-style05 {

		.scroller {
			--coca-image-compare-handle-stroke: 10px;
			background-color: #fff;
			-webkit-background-clip: padding-box;
			background-clip: padding-box;
			border-color: rgba(255, 255, 255, 0.53);
			border-style: solid;
		}
	}

	.template-style06 {

		.scroller {
			--coca-image-compare-handle-circle-height: 70px;
			--coca-image-compare-handle-circle-width: 15px;
			--coca-image-compare-handle-triangle-position: 15px;
			margin-left: calc(-1 * var(--coca-image-compare-handle-stroke) + 16px);

			.left-arrow {
				margin-left: calc(-1 * (var(--coca-image-compare-handle-stroke) + 25px));
			}

			.right-arrow {
				margin-right: calc(-1 * (var(--coca-image-compare-handle-stroke) + 25px));
			}
		}
	}
}
